import React, { Component, Fragment } from 'react';

import {
  Card,
  Row,
  Table,
  Popconfirm,
  Col,
  Menu,
  Icon,
  Cascader,
  Select,
  Checkbox,
  Input,
} from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { arrangeGV } from 'dump/data';
import './style.scss';
import SearchBarTrainingPlan from './FormTrainingPlan';
const { Option } = Select;
// eslint-disable-next-line react/prefer-stateless-function
class TrainingPlanTable extends Component {
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
  }

  render() {
    const columns = [
      {
        title: 'Tên lớp',
        dataIndex: 'className',
      },
      {
        title: 'Tên môn',
        dataIndex: 'name',
      },
      {
        title: 'Số học trình',
        dataIndex: 'SHT',
      },
      {
        title: 'Lớp ghép',
        dataIndex: 'classG',
      },
      {
        title: 'Loại giờ',
        dataIndex: 'LG',
      },
      {
        title: 'Tốp thứ',
        dataIndex: 'TT',
      },
      {
        title: 'S tiết CTĐT',
        dataIndex: 'ST',
      },
      {
        title: 'S tiết tuần',
        // dataIndex: 'STT',
        render: (text, record) => <Input style={{ width: '75px' }} value={record.STT} />,
      },
      {
        title: 'Từ tiết mấy',
        // dataIndex: 'TTM',
        render: (text, record) => <Input style={{ width: '75px' }} value={record.TTM} />,
      },
      {
        title: 'Đến tiết mấy',
        // dataIndex: 'DTM',
        render: (text, record) => <Input style={{ width: '75px' }} value={record.DTM} />,
      },
      {
        title: 'S tiết xếp KH',
        dataIndex: 'STXKH',
      },
      {
        title: 'S tiết chưa xếp KH',
        dataIndex: 'STCX',
      },
      {
        title: 'S tiết xếp TKB',
        dataIndex: 'STXTKB',
      },
      {
        title: 'Ca học',
        dataIndex: 'CH',
      },
      {
        title: 'Giai đoạn',
        dataIndex: 'GD',
      },
      {
        title: 'Từ tuần',
        dataIndex: 'TTT',
      },
      {
        title: 'Đến tuần',
        dataIndex: 'DT',
      },
      {
        title: 'Từ ngày',
        dataIndex: 'fromdate',
      },
      {
        title: 'Đến ngày ',
        dataIndex: 'todate',
      },
      {
        title: 'Ngày thi ',
        dataIndex: 'NT',
        width: '150px',
        render: (text, record) => (
          <Select style={{ width: '95%' }} defaultValue={record.NT}>
            <Option value="1">10/10/2015</Option>
            <Option value="2">12/10/2015 </Option>
            <Option value="3">1/10/2015 </Option>
          </Select>
        ),
      },
      {
        title: 'Ca thi ',
        dataIndex: 'CT',
        width: '150px',
        render: (text, record) => (
          <Select style={{ width: '95%' }} defaultValue={record.CT}>
            <Option value="1">4/10/2015 </Option>
            <Option value="2">5/10/2019 </Option>
            <Option value="3">10/1/2018 </Option>
          </Select>
        ),
      },
    ];
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
    };
    return (
      <Card title="Phân bổ giáo viên, ca học, phòng học">
        <Row>
          <Col span={24}>
            <Menu
              // selectedKeys={[this.state.current]}
              mode="horizontal"
              className="menu-item-form-brief"
            >
              <Menu.Item key="plus">
                <Icon type="save" theme="twoTone" />
                <a href="#create-subject/new">Lưu </a>
              </Menu.Item>
              <Menu.Item key="reload">
                <Icon type="plus-circle" theme="twoTone" />
                Tạo kế hoạch
              </Menu.Item>
              <Menu.Item key="reloads">
                <Icon type="delete" theme="twoTone" />
                Xóa
              </Menu.Item>
              <Menu.Item key="class">
                <Icon type="file" theme="twoTone" />
                Lớp môn học
              </Menu.Item>
              <Menu.Item key="XKH">
                <Icon type="snippets" theme="twoTone" />
                Xếp kế hoạch
              </Menu.Item>
              <Menu.Item key="delete">
                <Icon type="printer" theme="twoTone" twoToneColor="" />
                In kế hoạch
              </Menu.Item>
              <Menu.Item key="stop">
                <Icon type="file-excel" theme="twoTone" twoToneColor="green" />
                Xuất Excel
              </Menu.Item>
              <Menu.Item key="stop">
                <Icon type="stop" theme="twoTone" twoToneColor="red" />
                Thoát
              </Menu.Item>
            </Menu>
          </Col>
          <Col span={24}>
            <SearchBarTrainingPlan />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              scroll={{ x: 2000 }}
              rowSelection={rowSelection}
              bordered
              dataSource={arrangeGV}
              columns={columns}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingPlanTable />
  </Page>
);



